<template>
  <div :style="style" v-show="draftVisible" class="xdh-go-draft" v-draggable="dragable">
    <slot
      :close="close"
      :draftList="draftList"
      :remove="remove"
      :load="load"
      :save="save"
      :clear="clear"
    >
      <el-card class="xdh-go-draft__container">
        <div slot="header" class="xdh-go-draft__header">
          <span></span>
          <el-button
            type="text"
            style="float:right;padding:0;"
            circle
            icon="iconfont icon-multiply"
            size="mini"
            @click="close"
          ></el-button>
        </div>
        <div class="xdh-go-draft__content">
          <div v-for="(item, idx) in draftList" :key="idx" class="xdh-go-draft__item">
            <img :src="item.thumb" class="img" />
            <span class="txt">
              <div class="name">{{item.name}}</div>
              <div class="time">{{item.time}}</div>
            </span>
            <div class="btns">
              <i @click="remove(item)" class="iconfont icon-multiply remove"></i>
              <i @click="load(item)" class="iconfont icon-download load"></i>
            </div>
          </div>
        </div>
        <div class="xdh-go-draft__footer" @keyup.enter="save()">
          <el-input v-model="itemName" class="name-input" size="mini" placeholder=""></el-input>
          <el-button size="mini" type="primary" @click="save()" circle icon="iconfont icon-save"></el-button>
          <el-button size="mini" type="primary" @click="clear()" circle icon="iconfont icon-delete"></el-button>
        </div>
      </el-card>
    </slot>
  </div>
</template>
<script>
/**
 * XdhGoDraft
 * @module xdh-go-draft
 * @description 
 */
import go from 'gojs'
import draggable from '../../../utils/directives/draggable'
import { save, get, remove } from '../../../utils/storage'
import diagramManager from '../../../utils/dataManager/diagramManager'
/**
 * 
 * @member slots
 * @property {String} [default] ,slot-scope = 
 * @property {Function} [default.close] 
 * @property {Array} [default.draftList]  =  [{ json: '{ "class": "GraphLinksModel", "nodeDataArray": [ {"key":"A", "category":"a", "location":{"class":"go.Point", "x":0, "y":30.250027885742202}}, {"key":"B", "category":"b", "location":{"class":"go.Point", "x":83.13034326171876, "y":0}}, {"key":"C", "category":"c", "location":{"class":"go.Point", "x":83.13034326171876, "y":60.500055771484405}} ], "linkDataArray": [ {"from":"A", "to":"B"},{"from":"A", "to":"C"} ]}', name: 'ddd", thumb: 'data:image/png;base64, iVBORw0KGgoAAAANSUhEUgAAAB4AAAAeCAYAAAA7MK6iAAACG...', time: '2019/9/26 4:41:28 4:41:28' }]
 * @property {Function} [default.remove] 
 * @property {Function} [default.load] 
 * @property {Function} [default.save] 
 * @property {Function} [default.clear] 
 */
export default {
  name: 'XdhGoDraft',
  components: {},
  directives: {
    draggable
  },

  /**
   * 
   * @property {Boolean} [dragable=true] 
   * @property {String} [localKey=''] localStoragekey
   * @property {Object} [local=true] 
   * @property {String} [diagramName='dig'] go.Diagram
   * @property {Object} [visible.sync=false] 
   * @property {Object} [list=[]] local=true,
   *                            
   *                            
   *                           [{ 
   *                              json: '{ "class": "GraphLinksModel",  "nodeDataArray":
   *  [ {"key":"A", "category":"a",
   *  "location":{"class":"go.Point", "x":0, "y":30.250027885742202}},
   * {"key":"B", "category":"b", "location":{"class":"go.Point", "x":83.13034326171876, "y":0}},
   * {"key":"C", "category":"c", "location":{"class":"go.Point", "x":83.13034326171876, "y":60.500055771484405}} ],
   *   "linkDataArray": [ {"from":"A", "to":"B"},{"from":"A", "to":"C"} ]}',
                                  name: 'ddd",
                                  thumb: 'data:image/png;base64,
                                  iVBORw0KGgoAAAANSUhEUgAAAB4AAAAeCAYAAAA7MK6iAAACG...',
                                  time: '2019/9/26 4:41:28 4:41:28'
                                }]
   */
  props: {
    diagramName: {
      type: String,
      default: 'dig'
    },
    dragable: {
      type: Boolean,
      default: true
    },
    localKey: {
      type: String,
      default: ''
    },
    // 
    local: {
      type: Boolean,
      default: true
    },
    visible: {
      type: Boolean,
      default: false
    },
    list: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      draftList: [],
      imageDefaultOption: {
        size: new go.Size(30, 30),
        type: 'image/png'
      },
      itemName: '',
      draftVisible: false
    }
  },
  watch: {
    draftVisible(val) {
      this.$emit('update:visible', val)
    },
    visible(val) {
      this.draftVisible = val
    }
  },
  computed: {
    style() {
      return {
        width: '350px'
      }
    }
  },
  methods: {
    /**
     * @function
     * @name close
     * @description 
     */
    close() {
      this.draftVisible = false
    },
    /**
     * @function
     * @name save
     * @description 
     */
    save() {
      let bounds = diagramManager[this.diagramName].documentBounds
      let scaleW = 30 / bounds.width
      let scaleH = 30 / bounds.height
      let scale = Math.min(scaleW, scaleH)
      let options = Object.assign({}, this.imageDefaultOption, {
        scale: scale
      })
      let data = diagramManager[this.diagramName].makeImageData(options)
      let date = new Date()
      let item = {
        thumb: data,
        name: this.itemName,
        time: `${date.toLocaleString()} ${date.toLocaleTimeString()}`,
        json: diagramManager[this.diagramName].model.toJson()
      }
      this.draftList.push(item)
      this.$emit('save', item)
      if (this.local) {
        save('XDH-GO-DRAFT' + this.localKey, this.draftList, localStorage)
      }
      this.$emit('save')
    },
    /**
     * @function
     * @name remove
     * @description 
     * @param {Object} [item] 
     */
    remove(item) {
      let idx = this.draftList.indexOf(item)
      this.$emit('remove', this.draftList[idx])
      this.draftList.splice(idx, 1)
      if (this.local) {
        save('XDH-GO-DRAFT' + this.localKey, this.draftList, localStorage)
      }
    },
    /**
     * @function
     * @name load
     * @description 
     * @param {Object} [item] 
     */
    load(item) {
      diagramManager[this.diagramName].clear()
      diagramManager[this.diagramName].model = go.Model.fromJson(item.json)
    },
    /**
     * @function
     * @name clear
     * @description 
     */
    clear() {
      this.draftList = []
      this.$emit('clear')
      if (this.local) {
        remove('XDH-GO-DRAFT' + this.localKey, localStorage)
      }
    },
    destroy() {
      if (this.$el.parentNode) {
        this.$el.parentNode.removeChild(this.$el)
      }
    }
  },
  mounted() {
    document.body.appendChild(this.$el)
  },
  created() {
    this.draftVisible = this.visible
    if (this.local) {
      let draftList = get('XDH-GO-DRAFT' + this.localKey, localStorage)
      this.draftList = draftList || this.list
    } else {
      this.draftList = this.list
    }
  },
  beforeDestroy() {
    this.destroy()
  }
}
</script>
<style type="text/scss" lang="scss" scoped>
</style>
