
<template>
  <div :class="`xdh-go-search ${customClass}`" :style="customStyle" @keyup.enter="search">
    <slot
      :search="search"
      :keyword="keyword"
      :searchResult="searchResult"
      :resultCache="resultCache"
      :index="index"
      :popoverShow="popoverShow"
    >
      <el-popover v-model="popoverShow" ref="popover" trigger="manual" placement="bottom">
        <slot name="popover" :searchResult="searchResult" :keyword="keyword">
          <div>: {{searchResult.type}}</div>
          <div>: {{keyword}}</div>
          <div>: {{searchResult.keyword}}</div>
          <div>: {{searchResult.hit}}</div>
        </slot>
      </el-popover>
      <el-input
        v-popover:popover
        placeholder=""
        v-model="keyword"
        @change="keywordChange"
        size="small"
        class="xdh-go-search__input"
        clearable
      >
        <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
      </el-input>
    </slot>
  </div>
</template>
<script>
/**
 * XdhGoSearch
 * @module xdh-go-search
 * @description 
 */
/**
 * 
 * @member slots
 * @property {String} [default] ,slot-scope =
 * @property {Function} [default.search] 
 * @property {String} [default.keyword] input
 * @property {Array} [default.resultCache] 
 * @property {Function} [default.index] 
 * @property {Object} [default.searchResult] 
 * @property {String} [default.searchResult.keyword] 
 * @property {String} [default.searchResult.hit] 
 * @property {String} [default.searchResult.type] 
 * @property {Boolean} [default.popoverShow] popover
 */
/**
 * 
 * @member slots
 * @property {String} [popover] , defaultslot-scope =
 * @property {Object} [popover.keyword] 
 * @property {Object} [popover.searchResult] 
 * @property {String} [popover.searchResult.keyword] 
 * @property {String} [popover.searchResult.hit] 
 * @property {String} [popover.searchResult.type] 
 */
import diagramManager from '../../../utils/dataManager/diagramManager'

export default {
  name: 'XdhGoSearch',
  components: {},
  /**
   * 
   * @property {String} [diagramName='dig'] go.Diagram

   * @property {Boolean} [pullCenter=true] 
   * @property {Boolean} [ignoreCase=true] 
   * @property {Array} [nodeKeys=['key']] : ['info.name', 'remark.desc']
   * @property {Array} [linkKeys=['key']] : ['info.name', 'remark.desc']
   * @property {Object} [mode=node] , 'node'/'link'/'node,link'/'link,node'
   * @property {String} [customClass=''] 
   * @property {Object} [customStyle={}] 
   * @property {Number} [popDuration={}] 0
   */
  props: {
     diagramName: {
      type: String,
      default: 'dig'
    },
    pullCenter: {
      type: Boolean,
      default: true
    },
    ignoreCase: {
      type: Boolean,
      default: true
    },
    // key
    nodeKeys: {
      type: Array,
      default() {
        return ['key']
      }
    },
    // key
    linkKeys: {
      type: Array,
      default() {
        return ['key']
      }
    },
    //  
    mode: {
      type: String,
      default() {
        return 'node'
      },
      validator(val) {
        return ['node', 'link', 'node,link', 'link,node'].includes(val)
      }
    },
    customClass: {
      type: String,
      default: ''
    },
    customStyle: {
      type: Object,
      default() {
        return {}
      }
    },
    popDuration: {
      type: Number,
      default: 5000
    }
  },
  data() {
    return {
      keyword: '',
      // 
      keywordCache: '',
      // index
      resultCache: [],
      index: 0,
      searchResult: {},
      popoverShow: false,
      popTimeout: null
    }
  },
  computed: {},
  methods: {
    keywordChange() {
      this.searchResult = {}
    },
    search() {
      if (!this.keyword) {
        /**
         * 
         * @event on-error
         * @param {String} error 
         */
        this.$emit('on-error', 'keyword-empty')
        return
      }
      clearTimeout(this.popTimeout)
      // index1
      if (this.keyword === this.keywordCache) {
        this.index = (this.index + 1) % this.resultCache.length
      } else {
        this.index = 0
      }
      this.searchNode()
      if (this.resultCache.length !== 0) {
        this.searchResult = this.resultCache[this.index]
        if (this.popDuration && this.searchResult.hit) {
          this.popoverShow = true
        }
        this.popTimeout = setTimeout(() => {
          this.popoverShow = false
        }, this.popDuration)
        let node = this.searchResult.node
        node.isSelected = true
        if (this.pullCenter) {
          let rect = node.actualBounds
          diagramManager[this.diagramName].centerRect(rect)
        }
        /**
         * 
         * @event on-search
         * @param {Object} node 
         * @param {Array} resultCache 
         * @param {Number} index 
         * @param {String} keyword 
         */
        this.$emit(
          'on-search',
          node,
          this.resultCache,
          this.index,
          this.keyword
        )
      } else {
        this.$emit('on-error', 'no-result')
      }
    },
    searchNode() {
      this.keywordCache = this.keyword
      diagramManager[this.diagramName].clearSelection()
      let nodes = []
      // 
      if (this.mode.includes('node')) {
        diagramManager[this.diagramName].nodes.each(n => {
          let hit = false // 
          let hitKeywords = []
          let hitText = []
          this.nodeKeys.forEach(k => {
            let keywords = k.split('.')
            let d = this.getData(n.data, keywords)
            if (typeof d === 'string') {
              if (this.ignoreCase) {
                if (d.toLowerCase().includes(this.keyword.toLowerCase())) {
                  hit = true
                  hitKeywords.push(k)
                  hitText.push(`${d}`)
                }
              } else {
                if (d.includes(this.keyword)) {
                  hit = true
                  hitKeywords.push(k)
                  hitText.push(`${d}`)
                }
              }
            }
          })
          if (hit) {
            nodes.push({
              type: 'node',
              keyword: hitKeywords.join(','),
              hit: hitText.join('\n'),
              node: n
            })
          }
        })
      }
      if (this.mode.includes('link')) {
        diagramManager[this.diagramName].links.each(n => {
          let hit = false
          let hitKeywords = []
          let hitText = []
          this.linkKeys.forEach(k => {
            let keywords = k.split('.')
            let d = this.getData(n.data, keywords)
            if (typeof d === 'string') {
              if (this.ignoreCase) {
                if (d.toLowerCase().includes(this.keyword.toLowerCase())) {
                  hit = true
                  hitKeywords.push(k)
                  hitText.push(`${d}`)
                }
              } else {
                if (d.includes(this.keyword)) {
                  hit = true
                  hitKeywords.push(k)
                  hitText.push(`${d}`)
                }
              }
            }
            if (hit) {
              nodes.push({
                type: 'link',
                keyword: hitKeywords.join(','),
                hit: hitText.join('\n'),
                node: n
              })
            }
          })
        })
      }
      //   console.log(nodes.map(r => r.data.key))
      if (nodes.length !== 0) {
        this.resultCache = nodes
      } else {
        this.resultCache = []
      }
    },
    getData(data, keywords) {
      if (keywords.length !== 0) {
        let key = keywords.shift()
        if (data[key]) {
          return this.getData(data[key], keywords)
        } else {
          return ''
        }
      } else {
        return data
      }
    }
  },
  created() {}
}
</script>
<style type="text/scss" lang="scss" scoped>
</style>
