<template>
  <span class="xdh-go-snapshot">
    <slot :makeImage="makeImage" :makeImageData="makeImageData" :makeSvg="makeSvg">
      <span v-for="(item, idx) in layout.split(',')" :key="idx" class="btn">
        <el-button size="mini" type="primary" @click="makeImage()" v-if="item === 'image'"></el-button>
        <el-button
          size="mini"
          type="primary"
          @click="makeImageData()"
          v-if="item === 'imageData'"
        >base64</el-button>
        <el-button size="mini" type="primary" @click="makeSvg()" v-if="item === 'svg'">svg</el-button>
      </span>
    </slot>
  </span>
</template>
<script>
/**
 * XdhGoSnapshot
 * @module xdh-go-snapshot
 * @description 
 */
/**
 * 
 * @member slots
 * @property {String} [default] ,slot-scope =
 * @property {Function} [default.makeImage] image
 * @property {Function} [default.makeImageData] base64
 * @property {Function} [default.makeSvg] svg
 */
import go from 'gojs'
import diagramManager from '../../../utils/dataManager/diagramManager'

export default {
  name: 'XdhGoSnapshot',
  components: {},
  /**
   * 
   * @property {String} [layout='image,imageData,svg'] 
   * @property {String} [diagramName='dig'] go.Diagram

   * @property {Object} [options={image:{}, imageData: {},svg: {}}] 
   */
  props: {
    layout: {
      type: String,
      default: 'image,imageData,svg'
    },
    diagramName: {
      type: String,
      default: 'dig'
    },
    options: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      imageDefaultOption: {
        scale: 1,
        size: new go.Size(100, 100),
        type: 'image/png'
      },
      svgDefaultOption: {
        scale: 1,
        size: new go.Size(100, 100)
      }
    }
  },
  computed: {},
  methods: {
    makeImage() {
      let bounds = diagramManager[this.diagramName].documentBounds
      let size = new go.Size(bounds.width + 20, bounds.height + 20)
      let options = Object.assign(
        {},
        this.imageDefaultOption,
        {
          size: size
        },
        this.options.image || {}
      )
      let data = diagramManager[this.diagramName].makeImage(options)
      /**
       * 
       * @event on-snap
       * @param {String} type , image/imageData/svg
       * @param {HTMLImageElement|String|SVGSVGElement} type , image/imageData/svg
       */
      this.$emit('on-snap', 'image', data)
    },
    makeImageData() {
      let bounds = diagramManager[this.diagramName].documentBounds
      let size = new go.Size(bounds.width + 20, bounds.height + 20)
      let options = Object.assign(
        {},
        this.imageDefaultOption,
        {
          size: size
        },
        this.options.imageData || {}
      )
      let data = diagramManager[this.diagramName].makeImageData(options)
      this.$emit('on-snap', 'imageData', data)
    },
    makeSvg() {
      let bounds = diagramManager[this.diagramName].documentBounds
      let size = new go.Size(bounds.width + 20, bounds.height + 20)
      let options = Object.assign(
        {},
        this.svgDefaultOption,
        {
          size: size
        },
        this.options.svg || {}
      )
      let data = diagramManager[this.diagramName].makeSvg(options)
      this.$emit('on-snap', 'svg', data)
    }
  },
  created() {}
}
</script>
<style type="text/scss" lang="scss" scoped>
</style>