<template>
  <div :style="menuStyle">
    <slot></slot>
  </div>
</template>
<script>
/**
   * XdhHtml
   * @module xdh-go-html
   * @description go.HtmlInfo
   * @example
   * // use it with menu components
   *  <xdh-html ref="menu1" menu-name="menu1">
          <circle-menu
          :menu-list="listData1"
          @item-click="itemClick">
          </circle-menu>
      </xdh-html>
   */
/**
 * 
 * @member slot [default]
 */
import go from 'gojs'
let $ = go.GraphObject.make
export default {
  name: 'XdhGoHtml',
  inject: ['diagramName'],
  /**
   * 
   * @property {String} menuName 
   * @property {Function} [beforeShowMenu = () => {}] node,diagram,tool,menu, 
   * @property {Function} [showContextMenu] node,diagram,tool,menu,
   * @property {Object} [menuStyle={'z-index': 10000,position:'fixed'}] 
   * @property {Function} [hideContextMenu=null] diagram,tool,menu 
   * @property {Boolean} [autoHide=true] truetooltipcontextMenutooltipcontentMenudiagram
   */
  props: {
    menuName: {
      type: String,
      required: true
    },
    appendToBody: {
      type: Boolean,
      default: true
    },
    menuStyle: {
      type: Object,
      default() {
        return {
          'z-index': 10000,
          position: 'fixed'
        }
      }
    },
    beforeShowMenu: {
      type: Function,
      default: () => {}
    },
    showContextMenu: {
      type: Function
    },
    hideContextMenu: {
      type: Function
    },
    autoHide: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      menu: null, // $el 
      menuObj: null, // 
      name: 'XdhGoHtml'
    }
  },
  computed: {},
  methods: {
    /**
     * contextMenuHTMLInfo
     * @function bindMenu
     * @returns {go.HTMLInfo}
     */
    bindMenu() {
      this.menuObj = this.$children[0]
      this.menu = this.$children[0].$el
      // 
      this.menu.addEventListener(
        'contextmenu',
        function(e) {
          e.preventDefault()
          return false
        },
        false
      )
      let option = {
        show: this.showMenu,
        mainElement: this.menu
      }
      if (this.autoHide) {
        option.hide = this.hideMenu
      }
      return $(go.HTMLInfo, option)
    },
    // 
    showMenu(obj, diagram, tool) {
      this.beforeShowMenu(obj, diagram, tool, this.menu)
      // 
      // diagram lastInput
      if (this.showContextMenu) {
        this.showContextMenu(obj, diagram, tool, this.menu)
      } else {
        this.menu.style.display = 'block'
        let event = diagram.lastInput.event
        this.menu.style.left = event.pageX + 'px'
        this.menu.style.top = event.pageY + 'px'
      }
    },
    hideMenu(diagram, tool) {
      if (this.hideContextMenu) {
        this.hideContextMenu(diagram, tool, this.menu)
      } else {
        this.menu.style.display = 'none'
      }
    }
  },
  mounted() {
    if (this.appendToBody) {
      document.body.append(this.$children[0].$el)
    } else {
      this.$parent.$parent.$el.append(this.$children[0].$el)
    }
  }
}
</script>
