<template>
  <div class="xdh-go-tooltip">
    <slot :text="text">
      <div class="tooltip-text">
         {{text}}
      </div>
    </slot>
  </div>
</template>
<script>
/**
 * XdhGoTooltip
 * @module xdh-go-tooltiip
 * @description tooltip
 */
/**
 * 
 * @member slots
 * @property {String} [default] ,slot-scope =
 * @property {String} [default.text] 
 */
export default {
  name: 'XdhGoTooltip',
  components: {},
    /**
   * 
   * @property {String} [text=''] 
   */
  props: {
    text: {
      type: String,
      default: ''
    }
  },
  data() {
    return {}
  },
  computed: {},
  methods: {
  },
  created() {},
  beforeDestroy() {
    this.$el.parentNode.removeChild(this.$el)
  }
}
</script>
